
import styled from 'styled-components'
import color from './utility'
const style = {
primary: `
      background:${color.primary}
      color:white !important 
    `
}
const Portlet = styled.div`
 
  background: white
  box-shadow: 0 0 20px 1px #bec0c2
  border-radius: 4px
  margin-bottom:1rem
`
const PortletHeader = styled.div`
  border-bottom: solid 1px #eee
  padding:1rem 
  ${({primary}) => style.primary}
`
const PortletBody = styled.div`
  padding: 1.5rem 1rem
  display: ${({ flex }) => {
    if (flex) return 'flex'
    return 'block'
  }}
  flex-direction: ${({ flex, flexColumn }) => {
    if (flex) return 'row'
    if (flexColumn) return 'column'
    return 'none'
  }}
  flex-wrap: wrap
`
const PortletFooter = styled.div`
  padding: 1rem
  border-top: solid 1px #eee
  display: ${({ flex }) => {
    if (flex) return 'flex'
    return 'block'
  }}
  flex-direction: ${({ flex, flexColumn }) => {
    if (flex) return 'row'
    if (flexColumn) return 'column'
    return 'none'
  }}
  flex-wrap: wrap
  justify-content:flex-direction: ${({ center, spaceBetween }) => {
    if (center) return 'center'
    if (spaceBetween) return 'space-between'
    return 'center'
  }}
`

export { Portlet, PortletHeader, PortletBody, PortletFooter }



