import styled from 'styled-components'

const gridColNumber = 12
const colwidth = 100 / gridColNumber
const style = {
  demo: `
       border: solid 1px #2b95fd;
       height:50px;
       display:flex;
       align-items: center;
       justify-content: center;
       color:#2b95fd;
       transition:0.3s;
       &:hover{
       
       background:#2b95fd;
       color:white;
       }
      &:not(:first-child) {
        border-left: none;
      }  
     `,
  demoOne: `
      
       height:50px;
       display:flex;
       align-items: center;
       justify-content: center;
       background:#2b95fd;
       color:white;
       transition:0.3s;
       &:hover{
         border:solid 1px #2b95fd;
         color:#2b95fd;
         background:white;
       }
     
     `,

  col: {
    auto: `
     
      flex: 0 0 auto;
      width: auto;
      max-width: none;
    `,
    1: `
     
      flex: 0 0 ${colwidth}%;
      max-width: ${colwidth}%;
    `,
    2: `
     
       flex: 0 0 ${colwidth * 2}%;
      max-width: ${colwidth * 2}%;
    `,
    3: `
      
     flex: 0 0 ${colwidth * 3}%;
      max-width: ${colwidth * 3}%;
        `,
    4: `
     
       flex: 0 0 ${colwidth * 4}%;
      max-width: ${colwidth * 4}%;
        `,
    5: `
      
      flex: 0 0 ${colwidth * 5}%;
      max-width: ${colwidth * 5}%;
        `,
    6: `
      
      flex: 0 0 ${colwidth * 6}%;
      max-width: ${colwidth * 6}%;
    `,
    7: `
      
      flex: 0 0 ${colwidth * 7}%;
      max-width: ${colwidth * 7}%;
    `,
    8: `
      
      flex: 0 0 ${colwidth * 8}%;
      max-width: ${colwidth * 8}%;
    `,
    9: `
     
      flex: 0 0 ${colwidth * 9}%;
      max-width: ${colwidth * 9}%;
    `,
    10: `
      
      flex: 0 0 ${colwidth * 10}%;
      max-width: ${colwidth * 10}%;
    `,
    11: `
     
     flex: 0 0 ${colwidth * 11}%;
      max-width: ${colwidth * 11}%;
    `,
    12: `
     
      flex: 0 0 ${colwidth * 12}%;
      max-width: ${colwidth * 12}%;
    `
  },
  offset: {
    1: `margin-left: ${colwidth}%;`,
    2: `margin-left: ${colwidth * 2}%;`,
    3: `margin-left: ${colwidth * 3}%;`,
    4: `margin-left: ${colwidth * 4}%;`,
    5: `margin-left: ${colwidth * 5}%;`,
    6: `margin-left: ${colwidth * 6}%;`,
    7: `margin-left: ${colwidth * 7}%;`,
    8: `margin-left: ${colwidth * 8}%;`,
    9: `margin-left: ${colwidth * 9}%;`,
    10: `margin-left: ${colwidth * 10}%;`,
    11: `margin-left: ${colwidth * 11}%;`
  },
  align: {
    spaceBetween: `
        justify-content:space-between;
        `,
    spaceAround: `
        justify-content: space-around;
        `,
    flexEnd: `
        justify-content: flex-end;
        `,
    center: `
        justify-content: center;
        `
  },
  alignSelf: {
    auto: `     
      align-self: auto !important;
    `,
    start: `   
      align-self: flex-start !important;
    `,
    end: `  
      align-self: flex-end !important;
    `,
    center: `
      -ms-flex-item-align: center !important;
      align-self: center !important;
    `,
    baseline: `
      align-self: baseline !important;
    `,
    stretch: `
      align-self: stretch !important;
    `
  }
}
const Container = styled.div`
  width: 100%;
  padding: ${({fluid}) => {
    if (fluid) return '0'
    return '0 2rem'
  }};
`

const Row = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin: 0.5rem 0;
  ${({ align }) => align && style.align[align]};
`

const Col = styled.div`
  width: 100%;
  ${({ demo }) => demo && style.demo};
  ${({ demoOne }) => demoOne && style.demoOne};
  ${({ offset }) => offset && style.offset[offset]};
  ${({ col }) => col && style.col[col]};
  padding:1rem;
  ${({ alignSelf }) => alignSelf && style.alignSelf[alignSelf]};

  @media screen and (min-width: 768px) {
    ${({ sm }) => sm && style.col[sm]};
  }
  @media screen and (min-width: 992px) {
    ${({ md }) => md && style.col[md]};
  }
  @media screen and (min-width: 1200px) {
    ${({ lg }) => lg && style.col[lg]};
  }
`
export { Container, Row, Col }



